<template>
  <div class="user-warp">
    <app-header
      title="绑定重卡汇账号"
    ></app-header>
    <div class="user-main">
      <mt-field label="账号" placeholder="请输入您手机号码" v-model="tel">
      </mt-field>
      <mt-field label="密码" placeholder="请输入您的密码" v-model="password" type="password"></mt-field>
      <div class="desc">
        <p>还没有重卡汇账号？<router-link to="/home/set">立即注册</router-link></p>
      </div>
      <mt-button type="primary" plain size="large" style="margin-top: 1rem;" @click="bindAccount">提交</mt-button>
    </div>
  </div>
</template>

<script>
import AppHeader from '../../../components/header/header.vue'
import {bind} from '../../../api/login'
export default {
  name: 'User-Bind',
  data () {
    return {
      tel: '',
      password: ''
    }
  },
  methods: {
    bindAccount () {
      if (!this.tel || !this.password) return this.$toast('请输入完整')
      bind(this.tel, this.password)
        .then(data => {
          if (!data.state) return this.$toast(data.errormsg || '网络错误')
          this.$toast('绑定成功')
          this.$router.replace('/home')
        })
    }
  },
  components: {
    AppHeader
  }
}
</script>

<style lang="less" scoped>
  .user-warp{
    height: 100%;
    background-color: #e5e5e5;
    .user-main{
      width: 100%;
      position: absolute;
      padding-top: 50px;
      overflow: hidden;
      background-color: #e5e5e5;
      &:after{
        content: " ";
        height: 5rem;
        display: block;
      }
      .desc {
        margin-left: 1em;
      }
    }
  }
  .title{
    padding: .6rem;
    color: #999;
  }
</style>



